import popupLayer from '../../components/popup-layer/popup-layer.vue';
export default {
    components: {
        popupLayer
    },
    data(){
        return {
            show: false, // 遮罩层
            wl_show: false, // 物流选择
            checked: true,
            checked1: true,
            checked2: true,
            checked3: true,
            wl_list: [
                {name:'顺丰',price:'免邮',index:0},
                {name:'中通',price:'免邮',index:1},
                {name:'圆通',price:'￥20',index:2}
            ],
        }
    },
    methods: {
        // 选择运费
        changeShip(){
            this.$refs.popupRef.show();
        },
		closeMethod(){
			this.$refs.popupRef.close();
		},
    }
}
